import { useState } from "react";

const MyCarousel = () => {
    const [education, setEducation] = useState(4);
    const [experience, setExperience] = useState(3);
    const [skill, setSkill] = useState(2);

    const handleShow = (e) => {
        if (e == 'education') {
            setSkill(4);
            setEducation(3);
            setExperience(2);
        } else if (e == 'experience') {
            setEducation(4);
            setExperience(3);
            setSkill(2);
        } else if (e == 'skill') {
            setExperience(4);
            setSkill(3);
            setEducation(2);
        }
    }

    return (
        <div className="w-full h-full flex flex-col gap-8">
            <div className="flex items-center w-full gap-8 flex-wrap md:flex-nowrap">
                <div className="text-5xl font-semibold">Resume</div>
                <div className="flex flex-row justify-center md:justify-end items-center w-full bg-gray-200 dark:bg-primary bg-opacity-50 rounded-full">
                    <div className="flex flex-row w-full md:max-w-[600px]">
                        <div onClick={() => handleShow('education')} className={`text-lg py-4 font-semibold flex-grow basis-1 flex justify-center ${education == 3 && 'bg-secondary rounded-full'} cursor-pointer`}>Education</div>
                        <div onClick={() => handleShow('experience')} className={`text-lg py-4 font-semibold flex-grow basis-1 flex justify-center ${experience == 3 && 'bg-secondary rounded-full'} cursor-pointer`}>Experience</div>
                        <div onClick={() => handleShow('skill')} className={`text-lg py-4 font-semibold flex-grow basis-1 flex justify-center ${skill == 3 && 'bg-secondary rounded-full'} cursor-pointer`}>Skills</div>
                    </div>
                </div>
            </div>
            <div className="relative">
                <div className={`move${education} transition-transform absolute md:h-[500px] flex justify-center items-center`}>
                    <div className="grid grid-cols-1 gap-16 md:grid-cols-2">
                        <img src={require('./asset/education.png')} alt="" className='max-h-[300px] md:max-h-[400px] h-full m-auto' />
                        <div className='flex flex-col gap-6 justify-center'>
                            <div className="flex flex-row gap-4 items-center">
                                <img src={require('./asset/logo-universal-school-png-logo.png')} alt="" className='h-[100px] w-[100px]' />
                                <div className='flex flex-col gap-2'>
                                    <div className='text-2xl font-semibold'>Universal School Kemayoran</div>
                                    <div className='text-xl'>Elementary School - Junior High School - Senior High School of Science</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2010 - 2020</div>
                                </div>
                            </div>
                            <div className="flex flex-row gap-4 items-center">
                                <img src={require('./asset/site-logo-white-bu.png')} alt="" className='h-[100px] w-[100px]' />
                                <div className='flex flex-col gap-2'>
                                    <div className='text-2xl font-semibold'>Bina Nusantara University</div>
                                    <div className='text-xl'>Computer Science</div>
                                    <div className='text-lg text-gray-40 text-gray-4000dark: dark:text-gray-300'>2020 - present</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={`move${experience} transition-transform absolute md:h-[500px] flex justify-center items-center`}>
                    <div className="grid rid-cols-1 gap-16 md:grid-cols-2">
                        <img src={require('./asset/experience.png')} alt="" className='max-h-[300px] md:max-h-[400px] h-full m-auto' />
                        <div className='flex flex-col max-h-[400px] overflow-y-scroll'>
                            <div className='text-2xl mb-2 font-bold'>Organizational Experience</div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Activist of Web Development Division HIMTI Binus University</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2022 - 2023</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Technical Support of LDKP HIMTI 2023</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2023 - 2023</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Event Organizer Member of HIMTI Farewell 2023</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2023 - 2023</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Committee Member of Equipment and Supplies TECHFEST 2022</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2022 - 2022</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Committee Member of Business and Funds HIMTI Anniversay Event 2023</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2022 - 2022</div>
                                </div>
                            </div>
                            <div className='text-2xl my-2 font-bold'>Volunteer Experience</div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Freshmen Leader</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2022 - 2022</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Freshmen Partner</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2022 - 2023</div>
                                </div>
                            </div>
                            <div className="pl-[10px]">
                                <div className='flex flex-col gap-2 border-l-2 border-gray-800 dark:border-white p-6 relative'>
                                    <div className='bg-gray-800 dark:bg-gray-300 h-[20px] w-[20px] rounded-full absolute top-1/2 transform -translate-y-1/2 -left-[10px]'></div>
                                    <div className='text-xl font-semibold'>Mentor</div>
                                    <div className='text-lg text-gray-400 dark:text-gray-300'>2023 - present</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={`w-full move${skill} transition-transform absolute md:h-[500px] flex justify-center items-center`}>
                    <div className="grid grid-cols-1 gap-16 md:grid-cols-2 gap-y-6 gap-x-20 w-full" id='skills'>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/skill-icons_html.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>HTML</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[90%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    90%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/devicon_css3.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>CSS</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[70%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    70%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/skill-icons_javascript.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>Javascript</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[70%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    70%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/logos_php.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>PHP</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[60%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    60%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/skill-icons_laravel-dark.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>Laravel</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[80%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    80%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/skill-icons_flutter-dark.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>Flutter</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[50%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    50%
                                </div>
                            </div>
                        </div>
                        <div className='flex flex-row gap-4'>
                            <img src={require('./asset/skill-icons_react-dark.png')} alt="" className='w-[100px]' />
                            <div className='flex flex-col gap-2 justify-center w-full'>
                                <div className='text-2xl font-semibold'>React</div>
                                <div class="bg-gray-200 dark:bg-primary rounded-full h-4">
                                    <div class="bg-secondary h-4 rounded-full w-[60%]"></div>
                                </div>
                                <div className="flex flex-row justify-end">
                                    60%
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MyCarousel;