import { Modal } from 'flowbite-react';
import { useState } from 'react';

const Card = ({ title, category, image, languages, description, photo, link, code }) => {

    const [showModal, setShowModal] = useState(false);

    return (
        <div>
            <Modal show={showModal} onClose={() => setShowModal(false)}>
                <div className='bg-white dark:bg-gray-800 rounded-lg md:overflow-hidden relative text-gray-800 dark:text-gray-200'>
                    <div className='absolute right-2 top-0 z-10'>
                        <button className='text-lg shadow-lg font-semibold text-red-500' onClick={() => setShowModal(false)}>
                            <i class="fa-solid fa-xmark"></i>
                        </button>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 w-full'>
                        <div className='flex justify-center items-center h-[200px] md:h-full bg-gray-100 relative rounded-tl-lg rounded-tr-lg  md:rounded-tr-none overflow-hidden'>
                            <div className='absolute top-0 left-0 p-2 text-white bg-gray-800 bg-opacity-50'>{category}</div>
                            {image != '' ? <img src={require(`./asset/${image}`)} alt="" className='max-w-[300px]' /> : <div className='text-3xl text-secondary font-semibold'>{title}</div>}
                        </div>
                        <div className='flex flex-col py-4 px-6 md:h-[500px] overflow-hidden md:overflow-y-scroll'>
                            <div className='flex flex-row justify-between items-center mt-4'>
                                <div className='text-3xl font-semibold'>{title}</div>
                                <div className='flex flex-row items-center flex-wrap gap-2'>
                                    {photo && <div className='text-md'><a href={photo}><i class="fa-solid fa-image"></i></a></div>}
                                    {link && <div className='text-md'><a href={link}><i class="fa-solid fa-link"></i></a></div>}
                                    {code && <div className='text-md'><a href={code}><i class="fa-solid fa-code"></i></a></div>}
                                </div>
                            </div>

                            <div className='flex flex-row items-center flex-wrap gap-2'>
                                {languages.map((language) => (
                                    <div className={`text-md ${language} bg-white dark:bg-primary py-2 px-4 rounded-full shadow-lg mt-4 font-semibold`}>{language}</div>
                                ))}
                            </div>
                            <div className='mt-4'>
                                {description}
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>

            <div className='bg-white dark:bg-gray-800 rounded-xl overflow-hidden'>
                <div className='flex justify-center items-center h-[200px] bg-gray-200 relative'>
                    <div className='absolute top-0 left-0 p-2 text-white bg-gray-800 bg-opacity-50'>{category}</div>
                    {image != '' ? <img src={require(`./asset/${image}`)} alt="" className='max-w-[300px]' /> : <div className='text-3xl text-secondary font-semibold'>{title}</div>}
                </div>
                <div className='flex flex-row items-center flex-wrap gap-2 px-2'>
                    {languages.map((language) => (
                        <div className={`text-md ${language} bg-white dark:bg-primary py-2 px-4 rounded-full shadow-md mt-4 font-semibold`}>{language}</div>
                    ))}
                </div>
                <div className='p-4 h-[250px] overflow-hidden'>
                    {description}
                </div>
                <div className='flex justify-end'>
                    <button className='text-lg bg-white dark:bg-primary p-2 rounded-lg shadow-md m-4 font-semibold' onClick={() => {
                        setShowModal(true)
                    }}>Read More</button>
                </div>
            </div>
        </div>
    );
}

export default Card;

